var angularApp = angular.module('angularApp');

angularApp.factory('GitHubBadgeService', ['$resource', function($resource){
    var url = 'http://githubbadge.appspot.com/badge/muhd7rosli';
    var action = {
        'get' : {
            method : 'get',
            isArray : false,
            headers : {
                'Access-Control-Allow-Origin' : 'http://muhd7rosli.github.io/',
                'Accept': 'application/json'

            }
        }
    };
    return $resource(url, {}, action);
}]);